.posts-container-wrapper {
  height: 500px;
  width: 100%;
  min-width: 400px;
  margin: 5px auto;
}
.post-border {
  border: 0px solid #d3d3d3;
  margin: 15px 0;
  -webkit-box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.2);
}
.post-image-wrapper {
  height: 100%;
  width: 100%;
}

.post-image {
  width: 100%;
}

.post-header {
  display: -ms-flexbox;
  display: flex;
  background-color: #fafafa;
}

.post-thumb-wrapper {
  height: 50px;
  width: 50px;
}
.post-thumb {
  height: 100%;
  border-radius: 50%;
}
.like-section {
  display: -ms-flexbox;
  display: flex;
}

.like-section-wrapper {
  height: 10px;
  width: 10px;
  margin: 10px;
}



